@import "../../../../../../Assets/colorPallete/colorPallete.scss";
@import "../../../../../../Assets/fontFamily/nunitoSans.scss";

.facilityAddressFields {
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    > div {
        margin-right: 1.2em;
        width: 15%;
    }
    > div:nth-child(1) {
        width: 35%;
    }
    > div:nth-child(6) {
        width: 15%;
    }
}
