@import "../../../Assets/colorPallete/colorPallete.scss";
@import "../../../Assets/fontFamily/nunitoSans.scss";

.textField {
  width: 100%;
  height: 100%;
  padding: 0;
}
.rootTable {
  position: relative;
  margin-left: 2em;
  margin-right: 2em;
  width: 95% !important;
  height: auto;
  font-family: nunitoSansRegular;
  .tableHead {
    width: 90%;
    height: auto;
    background-color: $color-2;
    -webkit-user-select: none; /* Chrome all / Safari all */
    -moz-user-select: none; /* Firefox all */
    -ms-user-select: none; /* IE 10+ */
    user-select: none;

    .tableHeadRow {
      color: $color-3;
      width: 90%;
      height: auto;
      .tableCell {
        font-size: 1vw;
        width: auto;
        height: auto;
        .noTextField {
          transform: translateY(-65%);
        }
        .withTextfield {
          transform: translateY(20%);
          padding: 0;

          .showIcon {
            display: inline-block;

            .sortIcon {
              margin-left: 2em;
              display: inline-block;
            }
          }
        }
      }
    }
  }

  .tableBody {
    width: 100%;
    height: auto;
    .tableRow {
      width: 100%;
      height: auto;
      .tableCell {
        font-size: 0.9vw;
      }
    }
    .tableRow:nth-of-type(even) {
      background-color: white;
    }
    .tableRow:nth-of-type(odd) {
      background-color: $color-1;
    }
  }
}

.Pagination {
  padding-right: 3em !important;
}