@import "../../Assets/colorPallete/colorPallete.scss";
@import "../../Assets/fontFamily/nunitoSans.scss";

.root {
  width: 100%;
  padding: 10px;
  height: 100%;
  .cardTitleStyle {
    color: $color-12;
    margin-top: 0;
  }
  .cardContainerStyle {
    display: flex;
    flex-direction: column;
    height: 100%;
    .headerStyle {
      width: 100%;
      padding: 20px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      background: #dddfe3;
      .headerCountStyle {
        color: $color-9;
        margin: 0;
      }
      .headerTitleStyle {
        color: $color-12;
        margin: 0;
        font-weight: normal;
      }
    }
    .selectStyle {
      margin-bottom: auto;
      height: 100%;
      .selectValueStyle {
        color: $color-12;
        font-size: 0.9rem;
      }
    }
    .bodyStyle {
      width: 100%;
      .loadingComponent{
        width: 100%;
        height: 300px;
        display: flex;
        justify-content: center;
        align-items: center;
      }
    }
    .footerStyle {
      padding: 20px;
      margin-top: auto;
      width: 100%;
      display: flex;
      justify-content: flex-end;
      .footer {
        margin-left: auto;
        color: $color-9;
        font-weight: bold;
        text-decoration: none;
      }
    }
  }
}
