@import "../../../../Assets//colorPallete/colorPallete.scss";
@import "../../../../Assets/fontFamily/nunitoSans.scss";

.root {
  width: 100%;
  height: 100%;
  display: grid;
  flex-direction: row;
  grid-template-rows: 1fr;
  color: $color-12;
  font-family: nunitoSansRegular;

  .firstInput{
    display: flex;
    flex-direction: row;
    width: 100%;
    height: auto;
    margin-top: 2em;

    > div:nth-child(1) {
      width: 60%;
      margin-right: 1em;
    }
    > div:nth-child(2) {
      width: 40%;
      // margin-right: 1em;
    }
  }
}
.DialogActions {
  width: 100%;
  height: auto;
  padding-right: 2em;
  font-family: nunitoSansRegular;

  .Cancel {
    background-color: $color-10;
    color: white;
    height: 80%;
    transform: translate(-20%, -10%);
    padding-left: 2em;
    padding-right: 2em;
  }
  .Add {
    height: 80%;
    background-color: $color-8;
    color: white;
    padding-left: 2em;
    padding-right: 2em;
    transform: translate(-15%, -10%);
  }
}

.buttonAdd {
  margin-top: 2em !important;
  background-color: $color-9 !important;
  color: white !important;
  text-transform: initial !important;
  padding-left: 1.5em !important;
  padding-right: 1.5em !important;
  font-size: 0.8vw !important;
}
