@import "../../../Assets/colorPallete/colorPallete.scss";
@import "../../../Assets/fontFamily/nunitoSans.scss";
.root {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: auto;
    .content {
        padding: 20px;
        height: 100%;
        .buttonContainer {
            height: 100vh;
            display: flex;
            width: 100%;
            padding: 10px;
            flex-direction: column;
            justify-content: flex-end;
            align-items: center;
            .buttonGreen {
                width: 100%;
                background-color: $color-9;
                color: $color-1;
                margin-bottom: 10px;
            }
            .buttonRed {
                width: 100%;
                background-color: $color-10;
                color: $color-1;
            }
        }
    }
}
