@import "../../Assets/colorPallete/colorPallete.scss";
@import "../../Assets/fontFamily/nunitoSans.scss";

.root {
  .headerContainer {
    position: relative;
    width: 100%;
    height: auto;
    display: grid;
    padding: 0;
    grid-template-rows: 0.3fr 0.5fr;
    grid-auto-rows: minmax(100%, auto);
    z-index: 1;
    color: $color-12;

    .breadCrumbs {
      height: auto;
      padding-top: 20px;
      padding-bottom: 20px;
      padding-left: 2.5em;
      font-family: nunitoSansBlack;
      .breadCrumblistColor {
        color: $color-12 !important;
        text-decoration: none;
      }
      .lastTextColor {
        font-weight: bold;
        color: $color-12 !important;
        text-decoration: none;
      }
    }

    .secondContainer {
      width: 100%;
      height: auto;
      display: flex;
      padding-left: 2.5em;
      flex-direction: row;
      justify-content: space-evenly;
      color: $color-12;

      .headerTitle {
        display: flex;
        flex-direction: row;
        width: 100%;
        color: $color-12;
        padding-top: 0.5em;
        padding-bottom: 0.2em;
        font-size: 24px;
        letter-spacing: 0.1em;
        font-family: nunitoSansBlack;

        .nameTitle {
          width: auto;
          height: auto;
        }

        > label {
          margin-top: 1.1em;
          font-size: 12px;
          font-family: nunitoSansRegular;
          letter-spacing: 0.1em;
        }
        .indicator {
          margin-top: 0.6em;
          margin-left: 0.5em;
          margin-right: 0.2em;
          border: 1px solid $color-9;
          border-radius: 80%;
          width: 12px;
          height: 12px;
          background-color: $color-9;
        }
      }
      > div:nth-child(2) {
        flex: 1;
      }
    }
  }
}
.logoStyle {
  padding-left: 1.5em;
  flex: 1;

  img {
    max-height: 30px;
  }
}
