.list {
    width: 14%;
    height: 50%;
    position: absolute;
    display: flex;
    list-style-type: none;
    list-style: none;
    flex-direction: column;
    text-align: left;
    overflow-y: scroll;
    border: 1px solid lightgray;
    background-color: white;
    transform: translateY(-10%);
    z-index: 10;
    color: #000000;

    > li {
        cursor: pointer;
        -webkit-touch-callout: none;
        -webkit-user-select: none;
        -khtml-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
        z-index: 10;
    }

    > li:hover {
        font-weight: bolder;
    }
}
