@import "../../../../../Assets/colorPallete/colorPallete.scss";
@import "../../../../../Assets/fontFamily/nunitoSans.scss";

.root {
  width: 100%;
  height: 100%;
  color: $color-12;
  font-family: nunitoSansRegular;
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  .listStyles {
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: row;
    margin-bottom: 1.5em;
    text-transform: uppercase;

    .firstRow {
      width: 40%;
      height: auto;
      display: flex;
      flex-direction: row;

      > label:nth-child(1) {
        font-size: 0.9vw;
        font-family: nunitoSansRegular;
        width: 55%;
        height: auto;
        color: rgba(31, 58, 147, 0.5);
      }

      > label:nth-child(2) {
        font-size: 0.9vw;
        font-family: nunitoSansRegular;
        width: 75%;
        height: auto;
        color: $color-12;
      }
    }

    .secondRow {
      width: 50%;
      height: auto;
      display: flex;
      flex-direction: row;

      > label:nth-child(1) {
        font-size: 0.9vw;
        font-family: nunitoSansRegular;
        width: 36%;

        height: auto;
        color: rgba(31, 58, 147, 0.5);
      }

      > label:nth-child(2) {
        font-size: 0.9vw;
        font-family: nunitoSansRegular;
        width: 45%;
        height: auto;
        color: $color-12;
      }
    }
  }

  .listStyles:nth-child(14) {
    width: 100%;
  }
}

.changeName {
  display: flex;
  flex-direction: row;
  min-width: 100%;
  height: auto;
  color: $color-12;
  font-size: 15px;

  .TextField {
    width: 100%;
    height: auto;
  }

  .editIcon {
    display: flex;
    margin-right: 0.5em;
  }
}
