@import "../../../../../../Assets/colorPallete/colorPallete.scss";
@import "../../../../../../Assets/fontFamily/nunitoSans.scss";

.changeName {
    display: flex;
    flex-direction: row;
    min-width: 100%;
    height: auto;
    color: $color-12;
    font-size: 24px;

    .TextField {
        width: 100%;
        height: auto;
    }

    .editIcon {
        display: flex;
        margin-right: 0.5em;
    }
    .partnerName {
        transform: translateY(-15%);
    }
}

.root {
    display: flex;
    flex-direction: column;
    height: auto;
    width: 100%;
    font-family: nunitoSansRegular;
    color: $color-12;

    .wrapper {
        width: 100%;
        height: auto;

        .listStyles {
            width: 100%;
            height: auto;
            display: flex;
            flex-direction: row;
            margin-bottom: 1.5em;
            text-transform: uppercase;

            .firstRow {
                min-width: 100%;
                height: auto;
                display: flex;
                flex-direction: row;

                > label:nth-child(1) {
                    font-size: 0.9vw;
                    font-family: nunitoSansRegular;
                    width: 22%;
                    height: auto;
                    color: rgba(31, 58, 147, 0.5);
                }

                > label:nth-child(2) {
                    font-size: 0.9vw;
                    font-family: nunitoSansRegular;
                    width: 30%;
                    height: auto;
                    color: $color-12;
                }
            }
        }

        .listStyles2 {
            width: 100%;
            height: auto;
            display: flex;
            flex-direction: row;
