@import "../../../../../Assets/colorPallete/colorPallete.scss";
@import "../../../../../Assets/fontFamily/nunitoSans.scss";

.root {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  color: $color-3;
  font-family: nunitoSansRegular;
  text-align: center;
  font-size: 1vw;
}

.DialogActions {
  width: 100%;
  height: auto;
  position: relative;
  font-family: nunitoSansRegular;
  margin-top: 1.5em;

  > div:nth-child(1) {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    .Cancel {
      background-color: $color-10;
      color: white;
      width: 20%;
      height: 80%;
      margin-right: 1em;
      border-radius: 5px;
    }
    .Proceed {
      width: 20%;
      height: 80%;
      background-color: $color-8;
      color: white;
      margin-left: 1em;
      border-radius: 5px;
    }
  }
}
