@import "../../../../Assets/colorPallete/colorPallete.scss";
@import "../../../../Assets/fontFamily/nunitoSans.scss";

.editButton {
  position: absolute;
  display: flex;
  align-items: center;
  width: auto;
  height: auto;
  margin-right: 2.5em;
  right: 0;
  transform: translateY(-100px);
  z-index: 1;
  .buttonContainer {
    width: 100%;
    height: 100%;
    font-family: nunitoSansBold;
    color: white;
    background-color: $color-9;
    .insideText {
      font-weight: bold;
      font-size: 8px;
      display: flex;
      flex-direction: column;
      > text {
        flex: 1 1 1;
      }
    }
  }
}
