@import "../../../Assets/colorPallete/colorPallete.scss";
@import "../../../Assets/fontFamily/nunitoSans.scss";
.root {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: auto;
    .content {
        padding: 20px;
        height: 100%;
        .buttonContainer {
            height: 100vh;
            display: flex;
            justify-content: center;
            align-items: flex-end;
            .button {
                background-color: $color-9;
                color: $color-1;
            }
        }
    }
}
