@import "../../../../Assets//colorPallete/colorPallete.scss";
@import "../../../../Assets/fontFamily/nunitoSans.scss";

.root {
  width: 100%;
  height: 100%;
  display: grid;
  flex-direction: row;
  grid-template-rows: 1fr;
  color: $color-12;
  font-family: nunitoSansRegular;

  .firstInput{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: auto;
    margin-top: 2em;

    > div:nth-child(1) {
      width: 100%;
      // margin-right: 1em;
    }
    > div:nth-child(2) {
      width: 100%;
      // margin-right: 1em;
    }
  }
  .twoInput {
    display: inline-block;
    width: 50%;
    height: auto;
  }

}
.DialogContent {
  color: $color-12;
}
.DialogActions {
  width: 100%;
  height: auto;
  padding-right: 2em;
  font-family: nunitoSansRegular;

  .Cancel {
    background-color: $color-10;
    color: white;
    height: 80%;
    transform: translate(-20%, -10%);
    padding-left: 2em;
    padding-right: 2em;
    text-transform: initial;
  }
  .Add {
    height: 80%;
    background-color: $color-8;
    color: white;
    padding-left: 2em;
    padding-right: 2em;
    transform: translate(-15%, -10%);
    text-transform: initial;
  }
}

.facilityField {
  width: 70%;
}
