@import "../../Assets/colorPallete/colorPallete.scss";
@import "../../Assets/fontFamily/nunitoSans.scss";

.root {
    display: flex;
    flex-direction: row;
    width: 80%;
    height: auto;

    .numberFormat {
        width: 82%;
        height: 25px;

        .InputFieldTags {
            width: 100%;
            height: 25px;
            border-radius: 5px;
            border: 1px solid gray;
            border-top-right-radius: 0;
            border-bottom-right-radius: 0;
            padding-left: 0.5em;
            font-size: 16px;
            background-color: white;
            input::-webkit-outer-spin-button,
            input::-webkit-inner-spin-button {
                -webkit-appearance: none;
                margin: 0;
            }
        }

        .InputFieldTagsError {
            width: 100%;
            height: 25px;
            border-radius: 5px;
            border: 1px solid red;
            border-top-right-radius: 0;
            border-bottom-right-radius: 0;
            padding-left: 0.5em;
            font-size: 16px;
            background-color: white;
            input::-webkit-outer-spin-button,
            input::-webkit-inner-spin-button {
                -webkit-appearance: none;
                margin: 0;
            }
        }
    }

    .tags {
        width: 20%;
        height: 25px;
        text-align: center;
        left: 0;
        border-top-right-radius: 5px;
        border-bottom-right-radius: 5px;
        border-bottom: 1px solid gray;
        border-top: 1px solid gray;
        border-right: 1px solid gray;
        background-color: white;
        color: black;
        font-size: 16px;

        > label {
            width: 100%;
        }
    }
}

.InputFields {
    width: 80%;
    height: 25px;
    border-radius: 5px;
    border: 1px solid gray;
    padding-left: 0.5em;
    color: black;
    background-color: white;

    input::-webkit-outer-spin-button,
    input::-webkit-inner-spin-button {
        -webkit-appearance: none;
        margin: 0;
    }

    /* Firefox */
    input[type="number"] {
        -moz-appearance: textfield;
    }
}

.InputFieldsMask {
    width: 80%;
