@import "../../../../../../Assets/colorPallete/colorPallete.scss";
@import "../../../../../../Assets/fontFamily/nunitoSans.scss";

.editButton {
    position: absolute;
    display: flex;
    align-items: center;
    width: auto;
    height: auto;
    margin-right: 2.5em;
    right: 0;
    transform: translateY(-100px);
    z-index: 1;
    .buttonContainer {
        width: 100%;
        height: 100%;
        font-family: nunitoSansBold;
        color: white;
        background-color: $color-9;
        .insideText {
            font-weight: bold;
            font-size: 8px;
            display: flex;
            flex-direction: column;
            > text {
                flex: 1 1 1;
            }
        }
    }
}

.changeName {
    display: flex;
    flex-direction: row;
    min-width: 100%;
    height: auto;
    color: $color-12;
    font-size: 15px;

    .TextField {
        width: 100%;
        height: auto;
    }

    .editIcon {
        display: flex;
        margin-right: 0.5em;
    }
}

.root {
    display: flex;
    flex-direction: column;
    height: auto;
    width: 100%;
    font-family: nunitoSansRegular;
    color: $color-12;

    .FacilityAssignments {
        width: 100%;
        height: auto;

        > label {
            color: rgba(31, 58, 147, 0.5);
            font-size: 16px;
            margin-left: 0.5em;
        }
        .star {
            width: 10px;
            height: 10px;
        }

        .radioGroup {
            display: flex;
            width: 100%;
            height: auto;
            flex-direction: row;
            .errorMessage {
                font-size: 0.75rem;
                padding-left: 5px;
                color: red;
                // margin-left: 9.5em;
            }
        }

        .formComponent {
            width: 100%;
            height: auto;
            padding-left: 10.7em;
            .formGroup {
                width: 100%;
                height: auto;

                .checkLabel {
                    text-transform: initial;
                    font-size: 12px;
                }
                .exclusiveTo {
                    width: 100%;
                    height: auto;
                    display: flex;
                    align-items: center;
                    .label {
                        font-size: 15px;
                        margin-left: 1em;
                        margin-right: 1em;
                    }
                    .inputField {
                        width: auto;
                        height: 25px;
                        border-radius: 5px;
                        border: 1px solid grey;
                        padding-left: 0.5em;
                        color: $color-12;
                    }
                    .inputFieldDisabled {
                        width: auto;
                        height: 25px;
                        border-radius: 5px;
                        border: 1px solid #cbced6;
                        padding-left: 0.5em;
                        color: #1e2071;
                        background: #e1e4ea;
                    }

                    .errorMessage {
                        font-size: 0.75rem;
                        padding-left: 5px;
                        color: red;
                    }
                }
            }
        }
    }

    .medicalPartnerAndTvs {
        width: 100%;
        height: auto;
        display: flex;
        flex-direction: row;

        .medicalPartnerStatus {
            height: auto;
            width: 40%;
            > label {
                font-size: 16px;
                text-transform: uppercase;
                color: rgba(31, 58, 147, 0.5);
            }

            .medicalRadio {
                width: 100%;
                height: auto;

                .radioGroup {
                    width: 100%;
                    height: auto;

                    .radioLabel {
                        height: auto;
                        width: 100%;
                        font-size: 12px;
                        margin-bottom: 0 !important;
                    }
                }
            }
            .onHoldWrapper {
                display: flex;
                align-items: center;
                .radioLabel2 {
                    height: auto;
                    // width: 100%;
                    margin-right: 30px;
                    font-size: 12px;
                    margin-bottom: 0 !important;
                }
                span > label {
                    margin-right: 1em;
                    font-size: 15px;
                }
                .inputField {
                    width: auto;
                    height: 25px;
                    border-radius: 5px;
                    border: 1px solid grey;
                    padding-left: 0.5em;
                    color: $color-12;
                }

                .inputFieldDisabled {
                    width: auto;
                    height: 25px;
                    border-radius: 5px;
                    border: 1px solid #cbced6;
                    padding-left: 0.5em;
                    color: #1e2071;
                    background: #e1e4ea;
                }
            }
            .errorMessage {
                font-size: 0.75rem;
                padding-left: 5px;
                color: red;
            }
        }
        .Tvs {
            margin-left: 3.5em;
            height: auto;

            > label {
                color: rgba(31, 58, 147, 0.5);
            }
            font-size: 16px;
            > sub {
                font-size: 10px;
            }

            .medicalRadio {
                width: 100%;
                height: auto;

                .radioGroup {
                    width: 100%;
                    height: auto;

                    .radioLabel {
                        height: auto;
                        width: 100%;
                        font-size: 12px;
                        margin-bottom: 0 !important;
                    }
                }
            }
            .errorMessage {
                font-size: 0.75rem;
                padding-left: 5px;
                color: red;
            }
        }
    }
    .wrapper {
        width: 100%;
        height: auto;

        .listStyles {
            width: 100%;
            height: auto;
            display: flex;
            flex-direction: row;
            margin-bottom: 1.5em;
            text-transform: uppercase;

            .firstRow {
                width: 40%;
                height: auto;
                display: flex;
                flex-direction: row;

                > label:nth-child(1) {
                    font-size: 0.9vw;
                    font-family: nunitoSansRegular;
                    width: 55%;
                    height: auto;
                    color: rgba(31, 58, 147, 0.5);
                }

                > label:nth-child(2) {
                    font-size: 0.9vw;
                    font-family: nunitoSansRegular;
                    width: 75%;
                    height: auto;
                    color: $color-12;
                }
            }

            .secondRow {
                width: 50%;
                height: auto;
                display: flex;
                flex-direction: row;

                > label:nth-child(1) {
                    font-size: 0.9vw;
                    font-family: nunitoSansRegular;
                    width: 24%;

                    height: auto;
                    color: rgba(31, 58, 147, 0.5);
                }

                > label:nth-child(2) {
                    font-size: 0.9vw;
                    font-family: nunitoSansRegular;
                    width: 40%;
                    height: auto;
                    color: $color-12;
                }
            }
        }

        .listStyles:nth-child(3),
        .listStyles:nth-child(16) {
            width: 100%;

            height: auto;
            display: flex;
            flex-direction: row;
            margin-bottom: 1.5em;
            text-transform: uppercase;

            .firstRow {
                min-width: 100%;
                height: auto;
                display: flex;
                flex-direction: row;

                > label:nth-child(1) {
                    font-size: 0.9vw;
                    font-family: nunitoSansRegular;
                    width: 20.5%;
                    height: auto;
                    color: rgba(31, 58, 147, 0.5);
                }

                > label:nth-child(2) {
                    font-size: 0.9vw;
                    font-family: nunitoSansRegular;
                    width: 100%;
                    height: auto;
                    color: $color-12;

                    display: flex;
                    flex-direction: row;
                }
            }
        }

