@import "../../../../../../Assets/colorPallete/colorPallete.scss";
@import "../../../../.././../Assets/fontFamily/nunitoSans.scss";

.listStyles {
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: row;
    margin-bottom: 1.5em;
    text-transform: uppercase;

    .firstRow {
        min-width: 100%;
        height: auto;
        display: flex;
        flex-direction: row;

        > label:nth-child(1) {
            font-size: 0.9vw;
            font-family: nunitoSansRegular;
            width: 26%;

            height: auto;
            color: rgba(31, 58, 147, 0.5);
        }

        > label:nth-child(2) {
            font-size: 0.9vw;
            font-family: nunitoSansRegular;
            width: 100%;
            height: auto;
            color: $color-12;
        }
    }
}

.listStyles2 {
    width: 100%;
