@import "../../Assets/colorPallete/colorPallete.scss";
@import "../../Assets/fontFamily/nunitoSans.scss";

.FooterButtonContainer {
    position: -webkit-sticky;
    position: sticky;
    width: 100%;
    bottom: 0;
    height: auto;
    padding: 0;

    .FooterButtonWrapper {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        margin-top: 2%;
        width: 96%;
        height: auto;
        background-color: rgba(0, 0, 0, 0.3);
        padding-left: 3em;

        > div:nth-child(1) {
            padding-top: 1.5em;
            padding-bottom: 1.5em;
            .cancelButton {
                padding-top: 0.75em;
                padding-bottom: 0.75em;
                padding-right: 3em;
                padding-left: 3em;
                text-transform: capitalize;
            }
        }
        > div:nth-child(2) {
            flex: 1;
        }
        > div:nth-child(3) {
            display: flex;
            justify-content: space-evenly;
            padding-top: 1.5em;
            padding-bottom: 1.5em;
            flex-direction: row;
            margin-right: 3em;

            > div:nth-child(1) {
                display: flex;

                .SaveButton {
                    display: grid;
                    flex-direction: row;
                    padding-right: 1.5em;
                    padding-left: 1.5em;
                    background-color: rgba(38, 194, 129, 1);
                    color: white;
                }
            }

            > div:nth-child(1) {
                margin-left: 1em;
                .SaveBtn {
                    padding-top: 0.5em;
                    padding-bottom: 0.5em;
                    padding-right: 3em;
                    padding-left: 3em;
                    background-color: rgba(38, 194, 129, 1);
                    color: white;
                    text-transform: capitalize;
                }
            }
        }
    }
}
