@import "../../../Assets/colorPallete/colorPallete.scss";
@import "../../../Assets/fontFamily/nunitoSans.scss";

.root {
  position: absolute;
  left: 0;
  top: 0%;
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  
  .container {
    margin-right: 0.5em;
    width: 100%;
    display: grid;
    flex-direction: row;
    grid-template-rows: 0.3fr 2fr;

    .div-3 {
      background-color: $color-9 !important;
      text-transform: initial;
    }
    
    th:nth-child(1){
      width: 10%;
    }

    th:nth-child(2){
      width: 15%;
    }

    th:nth-child(3){
      width: 5%;
    }

    th:nth-child(4){
      width: 18%;
    }

    th:nth-child(5){
      width: 12%;
    }

    > div:nth-child(1) {
      background-color: $color-1;
      width: 100%;
      height: auto;
      display: flex;
      justify-content: flex-end;
      align-items: flex-end;
      padding-bottom: 0.4em;
      padding-right: 3em;
      .buttonAdd {
        margin-top: 2em;
        background-color: $color-9;
        color: white;
        text-transform: initial;
        padding-left: 1.5em;
        padding-right: 1.5em;
        font-size: 0.8vw;
      }
      .buttonUpdate {
        margin-top: 2em;
        background-color: $color-8;
        color: white;
        text-transform: initial;
        margin-left: 0.7em;
        padding-left: 1.5em;
        padding-right: 1.5em;
        font-size: 0.8vw;
      }
    }

    > div:nth-child(2) {
      background-color: $color-1;
      width: 100%;
      height: auto;
      display: flex;
    }
    > div:nth-child(3) {
      background-color: $color-4;
      width: 100%;
      height: auto;
      display: flex;
    }

    > div:nth-child(3) {
      background-color: transparent;
      width: 100%;
      height: auto;
      display: flex;
      justify-content: flex-end;
      align-items: flex-start;
      padding-top: 0.4em;
      padding-right: 0.5em;
      .button {
        background-color: $color-10;
        color: white;
        text-transform: initial;
        padding-left: 2.5em;
        padding-right: 2.5em;
        font-size: 1vw;
      }
    }
  }
  
  .FooterButtonContainer {
    position: -webkit-sticky;
    position: sticky;
    width: 100%;
    bottom: 0;
    height: auto;
    padding: 0;
  
    .FooterButtonWrapper {
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      margin-top: 2%;
      width: 100%;
      height: auto;
      background-color: rgba(0, 0, 0, 0.3);
      padding-left: 3em;
  
      > div:nth-child(1) {
        padding-top: 1.5em;
        padding-bottom: 1.5em;
        .cancelButton {
          padding-top: 0.75em;
          padding-bottom: 0.75em;
          padding-right: 3em;
          padding-left: 3em;
          text-transform: capitalize;
        }
      }
      > div:nth-child(2) {
        flex: 1;
      }
      > div:nth-child(3) {
        display: flex;
        justify-content: space-evenly;
        padding-top: 1.5em;
        padding-bottom: 1.5em;
        flex-direction: row;
        margin-right: 3em;
  
        > div:nth-child(1) {
          display: flex;
  
          .SaveButton {
            display: grid;
            flex-direction: row;
            padding-right: 1.5em;
            padding-left: 1.5em;
            background-color: rgba(38, 194, 129, 1);
            color: white;
          }
        }
  
        > div:nth-child(1) {
          margin-left: 1em;
          .SaveBtn {
            padding-top: 0.5em;
            padding-bottom: 0.5em;
            padding-right: 3em;
            padding-left: 3em;
            background-color: rgba(38, 194, 129, 1);
            color: white;
            text-transform: capitalize;
          }
        }
      }
    }
  }
}

.twoInput {
  display: inline-block;
  width: 50%;
}

.DialogActions {
  width: 100%;
  height: auto;
  padding-right: 2em;
  font-family: nunitoSansRegular;

  .Cancel {
    background-color: $color-10;
    color: white;
    height: 80%;
    transform: translate(-20%, -10%);
    padding-left: 2em;
    padding-right: 2em;
    text-transform: initial;
  }
  .Add {
    height: 80%;
    background-color: $color-8;
    color: white;
    padding-left: 2em;
    padding-right: 2em;
    transform: translate(-15%, -10%);
    text-transform: initial;
  }
}

.facilityField {
  width: 70%;
}
