@import "../../Assets/colorPallete/colorPallete.scss";
@import "../../Assets/fontFamily/nunitoSans.scss";

.rightBody {
  width: 830px;
  overflow-x: auto;
}

.textField {
  width: 200px;
  height: 100%;
  padding: 0;
}

.DownloadPreviewBtn {
  position: fixed !important;
  right: 30px;
  margin-top: 35px !important;
  background-color: $color-11 !important;
  color: #fff !important;
}

.DownloadPreviewBtnMedical {
  margin-top: 10px !important;;
}

.Pagination {
  margin-top: 10px;
  position: fixed;
  right: 30px;
}

.previewTable {
  border: 1px solid gray;

  td {
    border: 1px solid gray;
  }
}

.rootTable {
  height: auto;
  font-family: nunitoSansRegular;

  .tableHead {
    background-color: $color-2;
    -webkit-user-select: none; /* Chrome all / Safari all */
    -moz-user-select: none; /* Firefox all */
    -ms-user-select: none; /* IE 10+ */
    user-select: none;

    .withTextfield {
      min-width: 140px;
    }

    .showIcon {
      display: inline-block;
      padding-left: 5px;
    }

    .tableHeadRow {
      color: $color-3;
      height: auto;
    }
  }

  .tableBody {
    width: 100%;
    height: auto;

    .tableRow {
      width: 100%;
      height: auto;

      .tableCell {
        font-size: 0.9vw;

        input {
          width: 100px;
        }
      }
    }

    .tableRow:nth-of-type(even) {
      background-color: white;
    }

    .tableRow:nth-of-type(odd) {
      background-color: $color-1;
    }
  }
}

.DialogActions {
  width: 100%;
  height: auto;
  padding-right: 2em;
  font-family: nunitoSansRegular;

  .Cancel {
    background-color: $color-10;
    color: white;
    height: 80%;
    transform: translate(-20%, -10%);
    padding-left: 2em;
    padding-right: 2em;
    text-transform: initial;
  }

  .Add {
    height: 80%;
    background-color: $color-8;
    color: white;
    padding-left: 2em;
    padding-right: 2em;
    transform: translate(-15%, -10%);
    text-transform: initial;
  }
}
